import React, { Component } from 'react';
import './App.css';
import logo from './assets/meniu-fondo-amarillo.png';

class App extends Component {
  state = {
    params: {}
  }

  componentDidMount() {

    let params = {}

    let paramsRoute = this.props.location.search.substring(1).split('&');

    for (let param of paramsRoute) {
      params[param.split('=')[0]] = param.split('=')[1].replace('+', " ");
    }

    this.setState({
      params
    });
  }

  render() {

    let transactionState = parseInt(this.state.params.transactionState);
    return (
      <div className="container">

        <div className="row section">
          <div className="col s12 m6 offset-m3">
            <div className="card grey lighten-5">
              <div className="card-content white-text">
                <div className="row section">
                  <div className="col center offset-s4 s4">
                    <img className="responsive-img" alt="meniu-logo" src={logo} />
                  </div>
                </div>
                <span className="card-title center black-text">Resultado de tu compra</span>
                <br></br>
                <div className="section center">
                  <p className="black-text">{transactionState === 4 ?
                    "¡Tu compra fue realizada satisfactoriamente! Disfruta tu nuevo plan." :
                    transactionState === 6 ?
                      "Tu compra fue rechazada :(" :
                      transactionState === 5 ?
                        "El pago de la compra expiró. La compra no fue realizada correctamente." :
                        transactionState === 104 ?
                          "Hubo un error en la transacción. La compra no fue realizada correctamente." :
                          "El pago de tu compra se encuentra en estado pendiente. Por favor, revisa periódicamente tu correo para más información."}</p>
                </div>

              </div>
              <div className="card-action center">
                <i>Puedes cerrar este navegador y volver a la aplicación</i>
              </div>
            </div>
          </div>

        </div>

      </div>
    )
  }
}

export default App;
